














































































import Vue from 'vue';
import { mapGetters } from 'vuex';

export default Vue.extend({
  name: 'Coins',
  computed: {
    ...mapGetters(['coins']),
  },
  data: () => ({
    sortBy: 'rank',
    itemsPerPage: 20,
    fixedHeader: true,
    page: 1,
    pageCount: 0,
    sortDesc: false,
    loading: true,
    search: '',
    loadingText: 'Fetching Cryptocurrency Assets... please wait!',
    headers: [
      {
        text: 'RANK',
        align: 'start',
        value: 'rank',
      },
      {
        text: 'NAME',
        value: 'name',
      },
      {
        text: 'SYMBOL',
        value: 'symbol',
      },
      {
        text: 'MARKET CAP',
        value: 'marketCap',
      },
      {
        text: 'PRICE',
        value: 'price',
      },
      {
        text: '24HR',
        value: 'change',
      },
    ],
  }),
  methods: {
    toggleOrder() {
      this.sortDesc = !this.sortDesc;
    },
    toFixed(price: string): string {
      const result = parseFloat(parseFloat(price).toFixed(2)).toLocaleString();
      return `$ ${result}`;
    },
    getLink(item: { uuid: string; slug: string }) {
      return `https://coinranking.com/coin/${item.uuid}+${item.slug}`;
    },
    getColor(change: number) {
      if (change < 0) return 'red';
      if (change > 0) return 'green';
      return 'orange';
    },
  },
  watch: {
    coins() {
      if (this.coins.length > 0) {
        this.loading = false;
      }
    },
  },
});
